@use '../utils/mixins' as *;

.gallery-container {
  padding-top: 250px;
}

.gallery-item {
  display: flex;
}

.gallery-icon {
  margin-left: auto;
}

.gallery-icon__svg:not(:first-child) {
  margin-left: 14px;
}

.gallery-title__list {
  display: flex;
  gap: 20px;
  margin-top: 70px;
}

.gallery-title__img {
  border-radius: 22px;

  @include mobile {
    object-fit: cover;
    &:not(:first-child) {
      display: none;
    }
  }
}
